<template>
  <div class="box">
    <div class="box_">
      <div class="top">
        <img src="@/assets/images/my/bg2@2x.png" alt="" class="img">
        <ul class="pos">
          <li>{{number}}</li>
          <li>机器编号</li>
        </ul>
      </div>

      <div style="color:#333;padding:5pt;">请选择充电时长</div>

      <!-- 数据列表 -->
      <div class="list">
        <div class="item" :class="active==i?'act':''" v-for="(item,i) in list" :key="i" @click="active=i;Hours=item.time;getprice(i)">
          <img src="@/assets/images/my/2.png" alt="" class="icon">
          <div class="middle">
            <p style="">{{item.time}}小时</p>
            <p style="display:inline-block;margin-top:3pt;" v-if="parseInt(green_power/60)>0">可用环保电量兑换<strong style="color:#FF4C23;">{{parseInt(green_power/60)>item.time?item.time:parseInt(green_power/60)}}</strong>小时</p>
            <p style="display:inline-block;margin-top:3pt;">可获得<strong style="color:#FF4C23">{{item.green_power}}</strong>分钟环保电量</p>
          </div>
          <div class="right">
            <ul>
              <li><span>￥</span>{{parseInt(green_power/60)>0?(((Number(item.time)-parseInt(green_power/60))/Number(item.time)*Number(item.price)).toFixed(2)>0?((Number(item.time)-parseInt(green_power/60))/Number(item.time)*Number(item.price)).toFixed(2):0):item.price}}</li>
              <li class="del" v-if="parseInt(green_power/60)>0">￥{{Number(item.price)}}</li>
            </ul>
          </div>
        </div>
      </div>

      <!-- 优惠券 -->
      <div class="quan" @click="reg_quan()" v-if="reg==0">
        <van-cell title="点击注册有环保电量赠送哦">
            <van-icon
              slot="right-icon"
              name="arrow"
              style="line-height: inherit;"
            />
          </van-cell>
      </div>

      <!-- 协议 -->
      <div class="xieyi">
        <van-radio checked-color="#15BDDB" name="1" :value="value" @click="ready()">支付即代表阅读并同意</van-radio>
        <span @click="isshow=true">《机大呷充电使用协议》</span>
      </div>

    </div>
    <div class="bottom">
      <div>已选择 <span style="color:#FE7250;font-weight:600"> {{Hours}} </span> 小时，需支付<span style="color:#FE7250;font-weight:600">￥{{Number(yingfu).toFixed(2)}}元</span></div>
      <div class="uporder" @click="submit()">去付款</div>
    </div>
    
    <!-- 协议弹出层 -->
    <van-popup v-model="isshow" id="pop">
      <div class="xy">
        充电协议
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Cell, Icon, Radio, Popup, Toast} from 'vant';
import { gettaocan, getmima, gouporder, getcode } from "@/axios/getData";
export default({
  components:{
    vanCell: Cell,
    vanIcon: Icon,
    vanRadio: Radio,
    vanPopup: Popup
  },

  data(){
    return{
      //title:'暂无可用的优惠券',     // 充电优惠券与商城优惠券不一样   充电优惠券注册时送（一次而已） 没有优惠券就显示环保电量
      reg:0,   // 是否注册
      isshow:false,
      active:-1,
      index:0,
      value:'1',
      number:'JDX19A000001',                  // 设备编号
      list:[],  // 套餐列表
      green_power:0,    //  环保电量
      yingfu:0,         //  应付多少金额
      Hours:1,          //  选择几小时
    }
  },
  created(){
    let url = window.location.href
    try{
      this.number = url.split('number=')[1].split('&')[0];
      localStorage.setItem('openid',url.split('openid=')[1])
    }catch(err){}
    this.getlist();
    // this.getdata();
  },
  methods:{
    getprice(i){
      var item = this.list[i]
      // console.log(item)
      if(this.green_power>=60){
        this.yingfu = ((Number(item.time)-parseInt(this.green_power/60))/Number(item.time)*Number(item.price))>0?((Number(item.time)-parseInt(this.green_power/60))/Number(item.time)*Number(item.price)):0
      } else{
        this.yingfu = item.price
      }
    },
    reg_quan(){
      // 点击使用券或者使用环保电量
      // 如果没有注册跳到注册页面
      if(this.reg==0){
        this.$router.push({path:'/reg'})
      }
    },
    getname(){
      console.log(a,b)
    },
    ready(){
      if(this.value){
        this.value = 0;
        return;
      }  
      this.value='1';
    },
    async getdata(){
      let data = {};
      data.id = 2;
      data.number = 'JDX19A000001'
      data.number = this.number
      var res= await getmima(data);
      // console.log(res)
    },
    async getlist(){
      var res = await gettaocan({number:this.number});
      this.list = res.data.list
      this.green_power = res.data.green_power
      this.reg = res.data.is_register;
      // if(res.data.is_register==0){
      //   this.title='点击注册有环保电量赠送哦'
      // }
    },
    async submit(){
      var that = this;
      var data = {};
      var item = this.list[this.active]
      console.log(item)
      data.id = item.id                       //  套餐id  
      data.time = Number(item.time)*60        //  充电时间  分钟 
      data.price = item.price                 //  套餐原价  
      data.green_power = item.green_power     //  赠送的环保电量 
      data.key = item.key                     //  套餐id  
      data.number = this.number               //  套餐id  
      data.money = this.yingfu                //  充电实付款 
      // console.log(data);
      // return;
      gouporder(data).then((res)=>{
        if(res.code==200){
          try{
            if(res.data.password){
              this.$router.push({path:'/duihuan',query:{ password:JSON.stringify(res.data)}})
            } else{
              that.onBridgeReady(res)
            }
            return;
          }catch(err){
          }
        }
      });
    },
    onBridgeReady(res) {
      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: res.data.appId, //公众号名称，由商户传入
          timeStamp: res.data.timeStamp, //时间戳，自1970年以来的秒数
          nonceStr: res.data.nonceStr, //随机串
          package: res.data.package,
          signType: res.data.signType, //微信签名方式：
          paySign: res.data.paySign //微信签名
        },
        res => {
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            // Toast(res)
            var data = {}
            data.number = this.number
            // Toast(this.number)
            getcode( data ).then((res)=>{
              this.$router.push({path:'/duihuan',query:{ password:JSON.stringify(res.data)}})
            })
          }
          if (res.err_msg == "get_brand_wcpay_request:cancel") {
            Toast('支付失败')
            // this.$router.push('/')
          }
        }
     );
    },
  }
})
</script>

<style scoped lang="scss">
.box{
  height:100vh;
  .top{
    position: relative;
    .pos{
      position: absolute;
      top:40%;
      left:50%;
      transform: translate3d(-50%,-50%,0);
      li{
        text-align: center;
        color:#fff;
        font-size: 9pt;
      }
      li:nth-child(1){
        font-size: 17pt;
        font-weight: 600;
      }
    }
  }
  .list{
    padding:0 5%;
    .item{
      display: flex;
      align-items: center;
      padding:5pt;
      border:2pt solid transparent;
      .icon{
        margin-right: 10pt;
        width:48pt;
        height:48pt;
      }
      .middle{
        display: flex;
        flex-direction: column;
        padding:3pt 0;
        justify-content: space-between;
        width:50%;
        color:#444;
        font-size: 10pt;
        p:nth-child(1){
          font-size: 14pt;
          font-weight: 600;
        }
      }
      .right{
        margin-left:10pt;
        color:#FF4C23;
        font-weight: 600;
        font-size: 14pt;
        ul>li{
          text-align: center;
        }
        span{
          font-size: 12pt;
        }
      }
    }
  }
}
.act{
  border-color:#FF4C23 !important;
  border-radius: 6pt;
}

/* 优惠券 */
.quan{
  margin:5pt 5%;
  // margin-left:5%;
  width:90%;
  padding:10pt 0;
  padding-left:30pt;
  border-top:1pt solid #EFEFEF;
  border-bottom:1pt solid #EFEFEF;
  span{
    color:#999;
  }
}
/deep/ .van-cell{
  background: transparent;
  color:#999;
  padding:0;
}

/* 协议 */
.xieyi{
  display: flex;
  align-items: center;
  margin-top:20pt;
  padding-left:13pt;
  span{
    color:#15BDDB;
  }
}
#pop{
  width:90%;
}

/* 底部 */
.bottom{
  width:100%;
  height:10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top:1px solid #eee;
  padding: 0 5%;
  font-size: 12pt;
  .uporder{
    background: #15BDDB;
    padding:5pt 15pt;
    color:#fff;
    border-radius: 20pt;
    font-weight: 500;
  }
}
.img{
  height:80pt;
}
.del{
  color:#999;
  text-decoration: line-through;
  font-size: 12pt;
}
.box_{
  height:90%;
  overflow: scroll;
}
</style>


